$--blue: #0e0620;
$--white: #fff;
$--green: #2ccf6d;
.PageNotFound {
  svg {
    width: 100%;
    visibility: hidden;
  }
  h1 {
    font-size: 7.5em;
    margin: 15px 0px;
    font-weight: bold;
  }
  h2 {
    font-weight: bold;
  }
  .btn {
    z-index: 1;
    overflow: hidden;
    background: transparent;
    position: relative;
    padding: 8px 50px;
    border-radius: 30px;
    cursor: pointer;
    font-size: 1em;
    letter-spacing: 2px;
    transition: 0.2s ease;
    font-weight: bold;
    margin: 5px 0px;
    &.green {
      border: 4px solid $--green;
      color: $--blue;
      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 0%;
        height: 100%;
        background: $--green;
        z-index: -1;
        transition: 0.2s ease;
      }
      &:hover {
        color: $--white;
        background: $--green;
        transition: 0.2s ease;
        &:before {
          width: 100%;
        }
      }
    }
  }
}
